import { createPopper } from '@popperjs/core';

const getVersionSwitcher = (target, currentVersion, availableVersions) => {
    const versionSwitcher = document.createElement('div');

    const popover = document.createElement('div');
    popover.style.background = 'red';
    popover.style.color = 'white';
    popover.style.padding = '30px';
    popover.style.display = 'none';

    popover.appendChild(
        document.createTextNode(currentVersion)
    );

    const button = document.createElement('button');
    button.style.padding = '10px';
    button.style.position = 'fixed';
    button.style.right = '20px';
    button.style.bottom = '20px';
    button.style.width = 'auto';
    button.style.height = 'auto';
    button.appendChild(
        document.createTextNode(currentVersion)
    );

    button.addEventListener('click', () => {
        popover.style.display = 'block';
    });

    document.querySelector(target).appendChild(popover);
    document.querySelector(target).appendChild(button);

    createPopper(button, popover);
};

const element = document.createElement('div');
element.id = 'version-switcher';

document.body.appendChild(
    element
);

getVersionSwitcher('#version-switcher', '2.1', []);